import React, { useState } from 'react'
import { Form } from 'react-bootstrap'
import { todaydate, todaytime } from '../../../helpers/dateAndTime';
import { useForm } from '../../../hooks/useForm';
import { useGetInfo } from '../../../hooks/useGetInfo';
import { HomeNav } from '../../ui/HomeNav'
const axios = require('axios');

export const DataFacilitadoresAdd = () => {

    const tipo = "clases";
    const { data } = useGetInfo(tipo);

    

    const [ datos, handleInputChange, reset ] = useForm({
            name: '',
            lastname: '',
            address: '',
            telephone: '',
            email: '',
            activity: '',
            bio: '',
            service: '',
            clase: '',
            date: '',
            time: ''
        }
      );
    
    const [uploadimgname, setUploadimgname] = useState(null)
    const { name,lastname,address,telephone,email,activity,bio,service,clase } = datos;
    

    const handleSubmit = (data) => {
        data.preventDefault();
        let form_data = new FormData();
        form_data.append('name', name);
        form_data.append('lastname', lastname);
        form_data.append('address', address);
        form_data.append('telephone', telephone);
        form_data.append('email', email);
        form_data.append('activity', activity);
        form_data.append('bio', bio);
        form_data.append('service', service);
        form_data.append('clase', clase);
        form_data.append('date', todaydate);
        form_data.append('time', todaytime);
        form_data.append('uploadimgname', uploadimgname);
        let url = 'http://127.0.0.1:8000/api/facilitadoresdata/';
        const config = {
            headers: {
              'Content-Type': 'multipart/form-data' 
            },
        };
        axios.post(url, form_data, config)
            .then(res => {
                console.log(res.request.status);
                console.log(res.data);
            })
            .catch(err => console.log(err))
            console.log(uploadimgname)
        reset();
  };

  return (
    <>
        <HomeNav />
        <div className='container mb-5'>
            <div className='clases__text mt-5'>
                <h4 className='clases__h4'>SAMADHI STUDIO</h4>
                <h1 className='clases__h1'>Nuevo Facilitador</h1>
                <hr className='clases__hr'></hr>
            </div>

            <Form className='contacto__form' onSubmit={handleSubmit}>
                <Form.Group className="mb-3" controlId="formBasicEmail">
                    <Form.Control 
                        type="text" 
                        placeholder="Nombre"
                        name='name'
                        autoComplete='off' 
                        value={name}
                        onChange={handleInputChange}
                    />
                </Form.Group>

                <Form.Group className="mb-3" controlId="formBasicEmail">
                    <Form.Control 
                        type="text" 
                        placeholder="Apellido"
                        name='lastname'
                        autoComplete='off' 
                        value={lastname}
                        onChange={handleInputChange}
                    />
                </Form.Group>

                <Form.Group className="mb-3" controlId="formBasicEmail">
                    <Form.Control 
                        type="text" 
                        placeholder="Direccin"
                        name='address'
                        autoComplete='off' 
                        value={address}
                        onChange={handleInputChange}
                    />
                </Form.Group>

                <Form.Group className="mb-3" controlId="formBasicEmail">
                    <Form.Control 
                        type="text" 
                        placeholder="Telfono"
                        name='telephone'
                        autoComplete='off' 
                        value={telephone}
                        onChange={handleInputChange}
                    />
                </Form.Group>

                <Form.Group className="mb-3" controlId="formBasicEmail">
                    <Form.Control 
                        type="text" 
                        placeholder="Email"
                        name='email'
                        autoComplete='off' 
                        value={email}
                        onChange={handleInputChange}
                    />
                </Form.Group>

                <Form.Group className="mb-3" controlId="formBasicEmail">
                    <Form.Control 
                        type="text" 
                        placeholder="Actividad"
                        name='activity'
                        autoComplete='off' 
                        value={activity}
                        onChange={handleInputChange}
                    />
                </Form.Group>

                <Form.Group className="mb-3" controlId="exampleForm.ControlTextarea1">
                    <Form.Control 
                        as="textarea" 
                        rows={3} 
                        placeholder="Bio"
                        name='bio'
                        autoComplete='off' 
                        value={bio}
                        onChange={handleInputChange} 
                    />
                </Form.Group>

                <Form.Group className="mb-3" controlId="exampleForm.ControlTextarea1">
                    <Form.Control 
                        as="textarea" 
                        rows={3} 
                        placeholder="Servicio"
                        name='service'
                        autoComplete='off' 
                        value={service}
                        onChange={handleInputChange} 
                    />
                </Form.Group>

                <Form.Select 
                    aria-label="Clase"
                    onChange={handleInputChange}
                    value={clase}
                    name="clase"
                    >
                    <option>Clase</option>
                  {data.map( clases =>{
                     return(
                         <option value={clases.title}>{clases.title}</option>
                     )
                  })}
                </Form.Select>

                <Form.Group controlId="formFile" className="mb-3 mt-3">
                    <Form.Label>Selecciona una imagen</Form.Label>
                    <Form.Control
                        type="file"
                        name='uploadimgname'
                        onChange={(e)=>setUploadimgname(e.target.files[0])}
                    />
                </Form.Group>


                <button className='btn btn-green mt-4' type="submit">
                    Aadir
                </button>
            </Form>
         </div>
    </>
  )
}